import { styled, useTh } from "leva/plugin";
import IconEasing from "./icons/IconEasing";
import IconDistribution from "./icons/IconDistribution";
import IconBounce from "./icons/IconBounce";
import IconElastic from "./icons/IconElastic";
import { useEffect, useState } from "react";

const MenuItem = styled("div", {
	display: "flex",
	alignItems: "center",
	gap: "8px",
	cursor: "pointer",
	padding: "8px 12px",
	borderRadius: "4px",
	transform: "translateY(-7px)",
	opacity: 0,
	transition:
		"transform 120ms cubic-bezier(0.36, 0.94, 0.53, 1), opacity 120ms cubic-bezier(0.36, 0.94, 0.53, 1)",
	"&:hover": {
		backgroundColor: "black",
	},
	"&.visible": {
		transform: "translateY(0px)",
		opacity: 1,
	},
});

const IconContainer = styled("div", {
	width: "16px",
	height: "16px",
	minWidth: "16px",
	minHeight: "16px",
	maxWidth: "16px",
	maxHeight: "16px",
});

interface MenuItemProps {
	onClick: () => void;
	currentDisplay: string;
	visible: boolean;
	index: number;
}

export const EasingMenuItem = ({
	onClick,
	currentDisplay,
	visible,
	index,
}: MenuItemProps) => {
	const accentColor = useTh("colors", "accent2");
	const mainColor = useTh("colors", "highlight3");
	return (
		<MenuItem
			style={{
				color: currentDisplay === "bezier" ? accentColor : mainColor,
				transitionDelay: `${index * 45}ms`,
			}}
			onClick={onClick}
			className={visible ? "visible" : ""}
		>
			<IconContainer style={{}}>
				<IconEasing />
			</IconContainer>
			<div>Easing</div>
		</MenuItem>
	);
};

