function capitalizeFirstLetter(str: string): string {
	return str.charAt(0).toUpperCase() + str.slice(1);
}

// MenuItemButton.tsx
interface MenuItemButtonProps {
	onClick: () => void;
	label: string;
	currentDisplay: string;
	icon: React.ReactNode;
}

export const MenuItemButton: React.FC<MenuItemButtonProps> = ({
	onClick,
	label,
	currentDisplay,
	icon,
}) => {
	return (
		<div
			onClick={onClick}
			className={` px-2 py-[3px] w-fit rounded-md cursor-pointer outline-none ${
				currentDisplay === label
					? "bg-blue text-highlight3"
					: "bg-transparent hover:bg-elevation3 text-highlight2 hover:text-highlight2"
			}`}
		>
			<div className="hidden sm:flex text-sm">
				{label === "bezier" ? "Easing" : capitalizeFirstLetter(label)}
			</div>
			<div className="w-[18px] h-[18px] text-white sm:hidden flex">{icon}</div>
		</div>
	);
};
